/* Loader */
.loader{position: absolute;top:50%;left:50%;transform:translate(-50%, -50%) translateZ(0);margin:auto;display:inline-block;width:auto;will-change: transform;}
.loader .loading-text{white-space: nowrap;color:#fff;text-transform:uppercase;margin-top:25px;letter-spacing:2px;font-weight:100;font-size:1.5rem;}
.loader .loading-text span{display:inline-block; animation: wave 8s ease-in-out infinite;transform: translateY(30px) translateZ(0);will-change: transform;}


.aquarium{border:2px solid #fff; width:80%;margin:auto;height:60px;overflow:hidden;position: relative;}
.aquarium .water{width:150%;margin:auto;height:100%;animation: aquarium-anim infinite 4s;position: relative;}
.aquarium .water:before{content:"";width:175%;height:100%;background:rgba(66, 134, 218, 0.7)  url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAECAYAAACtBE5DAAAAF0lEQVQImWNggIL///83MGADWCVIUg0A9PQN9bgobnYAAAAASUVORK5CYII=) repeat 0 0;transform: translateZ(0); will-change: transform;position: absolute;top:0;right:0;}

.jellyfish{filter: drop-shadow(0px 0px 1px #fff);position:relative;left:0;top:50%;animation:jellyfish-move-anim 4s infinite;transform:translate(-50%, 0%) translateZ(0);will-change:transform;z-index:1;}


.jellyfish .head{width:25px;height:25px;position:absolute;background-color:#fff;transform:rotate(90deg) translateZ(0);will-change: transform;border:2px solid #fff;border-radius:20px;top:0;bottom:0;animation:jellyfish-head-anim infinite 1s;margin:auto; }
.jellyfish .body{width:100%;height:50px;position:relative;z-index:1;left:0;bottom:-15px;animation:jellyfish-body-anim infinite 1s;transform:translateZ(0);will-change: transform;}
.jellyfish .body .legs{width:3px;background:#fff;height:100%;position:absolute;border-radius:10px;animation:jellyfish-legs-anim infinite 1s}
.jellyfish .body .legs:nth-child(1),
.jellyfish .body .legs:nth-child(3){width:15px;border:3px solid transparent;border-radius:0px;background-color:transparent;}
.jellyfish .body .legs:nth-child(1){left:-8px;border-right-color:#fff;border-bottom-right-radius:75%;}
.jellyfish .body .legs:nth-child(2){left:50%;transform:translateX(-50%) translateZ(0);height:97.5%;will-change: transform;}
.jellyfish .body .legs:nth-child(3){right:-8px;border-left-color:#fff;border-bottom-left-radius:75%;}


.jellyfish:nth-child(1){animation-delay:0s;}
.jellyfish:nth-child(2){top:150px;animation-delay:2s;}
.jellyfish:nth-child(3){top:250px;animation-delay:5.25s;}
@keyframes aquarium-anim{
    0%{transform:translateX(0%) translateZ(0)}
    20%{transform:translateX(-3%) translateZ(0)}
    30%{transform:translateX(17%) translateZ(0)}
    45%{transform:translateX(12%) translateZ(0)}
    55%{transform:translateX(27.5%) translateZ(0)}
    70%{transform:translateX(22.5%) translateZ(0)}
    80%{transform:translateX(40%) translateZ(0)}
    95%{transform:translateX(35%) translateZ(0)}
    100%{transform:translateX(50%) translateZ(0)}
}

@keyframes jellyfish-move-anim{
    0%{transform:translateX(-13%) translateZ(0);}
    20%{transform:translateX(-13%) translateZ(0);}
    30%{transform:translateX(22.5%) translateZ(0);}
    45%{transform:translateX(22.5%) translateZ(0);}
    55%{transform:translateX(50%) translateZ(0);}
    70%{transform:translateX(50%) translateZ(0);}
    80%{transform:translateX(80%) translateZ(0);}
    95%{transform:translateX(80%) translateZ(0);}
    100%{transform:translateX(110%) translateZ(0);}
}

@keyframes jellyfish-head-anim{
     0%{width:25px;height:25px;border-radius:75%;border-top-left-radius:50px;border-top-right-radius:50px;transform: translateX(20px) rotate(90deg) translateZ(0);}
    65%{width:40px;height:18px;border-top-left-radius:50px;border-top-right-radius:50px;transform: translateX(0px) rotate(90deg) translateZ(0);}
    70%{width:40px;height:18px;border-top-left-radius:50px;border-top-right-radius:50px;transform: translateX(0px) rotate(90deg) translateZ(0);}
    100%{width:25px;height:25px;border-radius:75%;border-top-left-radius:50px;border-top-right-radius:50px;transform: translateX(20px) rotate(90deg) translateZ(0);}
}

@keyframes jellyfish-body-anim{
    0%{transform:translateY(-15px) translateZ(0);height: 50px;}
    65%{transform:translateY(-5px) translateZ(0);height: 40px;}
    70%{transform:translateY(-5px) translateZ(0);height: 40px;}
    100%{transform:translateY(-15px) translateZ(0);height: 50px;}
}

@keyframes jellyfish-legs-anim{
    0%{border-bottom-left-radius:0%;border-bottom-right-radius:0%;}
    65%{border-bottom-left-radius:75%;border-bottom-right-radius:75%;}
    70%{border-bottom-left-radius:75%;border-bottom-right-radius:75%;}
    100%{border-bottom-left-radius:0%;border-bottom-right-radius:0%;}
}


/* END Loader */

@media screen and (max-width: 640px) {
    .loader .loading-text{margin:auto;margin-top: 15px;white-space: normal;width:350px;line-height:200%;letter-spacing:5px;}
    .loader{width:90%;}
    .aquarium{width: 90%;}
}

@media screen and (max-width: 960px) {
    .loader .loading-text{font-size:1rem;}
}

@media screen and (max-width: 480px) {
    .loader .loading-text{letter-spacing:2px;width:260px;}
}






